import React, { useCallback } from "react";
import { SwitchProps } from "formik-material-ui";
import ReCaptcha from "./ReCaptcha";
import { store } from "../../../_store/store.config";

export function Captcha(props: SwitchProps) {
  const {
    form: { setFieldValue, errors, touched },
    field: { name },
  } = props;

  const theme = store.getState().auth.theme;
  const onChange = useCallback(
    (event) => {
      setFieldValue(name, event ? true : false);
    },
    [setFieldValue, name]
  );
  return (
    <ReCaptcha
      action="login"
      theme={theme}
      onChange={onChange}
      onError={touched.recaptcha && errors.recaptcha}
    />
  );
}
