import React from "react";
import * as Yup from "yup";
import { connect } from "react-redux";
import { bindActionCreators } from "redux";
import { ThunkDispatch } from "redux-thunk";
import { Field, Form, Formik } from "formik";
import { TextField } from "formik-material-ui";
import LockOutlinedIcon from "@material-ui/icons/LockOutlined";
import {
  Avatar,
  CssBaseline,
  Box,
  Button,
  Container,
  Typography,
  LinearProgress,
} from "@material-ui/core";

import { AppState } from "../../_types";
import { AppActions } from "../../_types";
import { ILost } from "../../_types/auth.types";
import { lostRequest } from "../../_actions/user.actions";
import { Captcha } from "../Layout/Captcha/Captcha";
import { push } from "connected-react-router";
import { Path } from "history";

interface LostPageProps {
  loading: boolean;
  loggedIn: boolean;
}

type Props = LostPageProps & LinkDispatchProps;

class LostPage extends React.Component<Props> {
  componentDidMount() {
    if (this.props.loggedIn) {
      this.props.push("/");
    }
  }
  render() {
    const { loading, loggedIn } = this.props;
    return !loggedIn ? (
      <Container component="main" maxWidth="xs" style={{ paddingTop: "10px" }}>
        <CssBaseline />
        <div style={{ textAlign: "center" }}>
          <Avatar style={{ margin: "auto" }}>
            <LockOutlinedIcon />
          </Avatar>
          <Typography component="h1" variant="h5">
            Lost Password
          </Typography>
          <Formik
            initialValues={{
              user_login: "",
              recaptcha: false,
            }}
            validationSchema={Yup.object().shape({
              user_login: Yup.string()
                .max(255)
                .required("Email or Username is required"),
              recaptcha: Yup.boolean().required("ReCAPTCHA is required"),
            })}
            onSubmit={(event: ILost, { resetForm }) => {
              if (event.user_login) {
                this.props.lostRequest(event);
              }
              resetForm({
                values: {
                  user_login: "",
                  recaptcha: event.recaptcha,
                },
              });
            }}
          >
            <Form>
              <Box my={2}>
                <Typography
                  color="textSecondary"
                  align="center"
                  gutterBottom
                  variant="body2"
                >
                  Enter your email or user_login
                </Typography>
              </Box>
              <Box my={1}>
                <Field
                  component={TextField}
                  color="secondary"
                  fullWidth
                  label="Email Address/Username"
                  margin="normal"
                  name="user_login"
                  id="user_login"
                  placeholder="John"
                  type="user_login"
                  variant="outlined"
                />
              </Box>
              <Box my={1}>
                <Field
                  component={Captcha}
                  color="secondary"
                  fullWidth
                  label="Recaptcha"
                  margin="normal"
                  name="recaptcha"
                  type="recaptcha"
                  variant="outlined"
                />
              </Box>
              {loading && <LinearProgress color="secondary" />}
              <Box my={1}>
                <Button
                  color="secondary"
                  disabled={loading}
                  fullWidth
                  size="large"
                  type="submit"
                  variant="contained"
                >
                  Recover
                </Button>
              </Box>
            </Form>
          </Formik>
        </div>
      </Container>
    ) : null;
  }
}
interface LinkDispatchProps {
  lostRequest: (user: ILost) => void;
  push: (path: Path) => void;
}

const mapStateToProps = (state: AppState, ownProps: Props): LostPageProps => {
  const { loggedIn, loading } = state.auth;
  return {
    loggedIn,
    loading,
  };
};

const mapDispatchToProps = (
  dispatch: ThunkDispatch<any, any, AppActions>,
  ownProps: LostPageProps
): LinkDispatchProps => ({
  lostRequest: bindActionCreators(lostRequest, dispatch),
  push: bindActionCreators(push, dispatch),
});

export default connect(mapStateToProps, mapDispatchToProps)(LostPage);
