import {
  createStyles,
  LinearProgress,
  Theme,
  Typography,
  WithStyles,
  withStyles,
} from "@material-ui/core";
import { FormikErrors } from "formik";
import * as React from "react";

declare global {
  interface Window {
    captchaOnLoad: () => void;
    grecaptcha: ReCaptchaInstance;
    webkitAudioContext: {
      new (contextOptions?: AudioContextOptions | undefined): AudioContext;
      prototype: AudioContext;
    };
    zones: any;
  }
}

interface ReCaptchaInstance {
  ready: (cb: () => any) => any;
  execute: (options: ReCaptchaExecuteOptions) => Promise<string>;
  render: (id: string, options: ReCaptchaRenderOptions) => any;
}

interface ReCaptchaExecuteOptions {
  action: string;
}

interface ReCaptchaRenderOptions {
  callback: () => void;
  sitekey: string;
  size: "normal";
  theme: "dark" | "light";
}

const useStyles = (theme: Theme) =>
  createStyles({
    root: {
      position: "relative",
      minHeight: 80,
    },
    captcha: {
      zIndex: 2,
      "& > div:first-child": {
        margin: "auto",
        "& > div > iframe": {
          borderRadius: 4,
          border: "1px solid",
        },
      },
    },
    captchaWarning: {
      "& > div:first-child": {
        margin: "auto",
        "& > div > iframe": {
          borderRadius: 4,
          border: "1px solid red",
        },
      },
    },
  });

interface Props extends WithStyles<typeof useStyles> {
  action: string;
  theme: "light" | "dark";
  onError: error;
  onChange: (val: boolean) => void;
}
type error =
  | string
  | false
  | FormikErrors<any>
  | string[]
  | FormikErrors<any>[]
  | undefined;

interface State {
  readonly loading: boolean;
}

class ReCaptcha extends React.PureComponent<Props, State> {
  state: State = {
    loading: true,
  };

  private script!: HTMLScriptElement;

  componentDidMount(): void {
    this.loadScript();
  }

  componentWillUnmount(): void {
    document.body.removeChild(this.script);
  }

  render() {
    const { classes, onError } = this.props;

    return (
      <div className={classes.root}>
        {this.state.loading && <LinearProgress color="secondary" />}
        <div
          id="g-recaptcha"
          className={onError ? classes.captchaWarning : classes.captcha}
        />
        <Typography color="error" variant="caption">
          {onError}
        </Typography>
      </div>
    );
  }

  private loadScript = (): void => {
    window.captchaOnLoad = this.onLoad;

    const url = "https://www.google.com/recaptcha/api.js";
    const queryString = "?onload=captchaOnLoad&render=explicit&hl=fa";
    const script = document.createElement("script");
    script.type = "text/javascript";
    script.src = url + queryString;
    script.async = true;
    script.defer = true;
    this.script = document.body.appendChild(script);
  };

  private callback = () => {
    this.props.onChange(true);
  };

  private onLoad = (): void => {
    window.grecaptcha.render("g-recaptcha", {
      callback: this.callback,
      sitekey: "6Le8wccUAAAAAA0kNONoUC-NKPecGnUGKRY7zMno",
      size: "normal",
      theme: this.props.theme,
    });

    window.grecaptcha.ready(() => {
      this.setState({ loading: false });
    });
  };
}

export default withStyles(useStyles)(ReCaptcha);
